import {
	mapGetters
} from 'vuex'
import {
	getRegionText
} from '@/utils/tools.js'
/**
 * 商品申报查询数据渲染组件
 */
export default {
	props: {
		dataList: {
			type: Array,
			default: function() {
				return []
			}
		}
	},
	data() {
		return {
			regionTxt: ''
		}
	},
	computed: {
		...mapGetters({
			userInfo: 'user/userInfo'
		})
	},
	created() {
		this.regionTxt = getRegionText(this.userInfo.region)
	}
}
